*{
    margin: 0px;

}

body{
    font-family: 'Exo', sans-serif;
    background: #002D62;  
    background: -webkit-linear-gradient(to left, #FDBB30, #BEC0C2);  
    width: 100%;
    height:100vh;
}


.context {
    width: 100%;
    position: absolute;
    top:5vh;
    text-align: center;

    
}

.button {
    color: "#FDBB30";
    align-content:center;
    padding: 50px;
}

.context h1{
    text-align: center;
    color: #FDBB30;
    font-size: 50px;
}

.context p{
    text-align: center;
    color: #FDBB30;
    font-size: 20px; 
}

.context span {
    text-align: center;
    color: #e3e3e4;
    font-size: 40px;
    font-weight: bold;
    font-style: italic;
}

.context span b{
    text-align: center;
    color: #FDBB30;
    font-size: 30px;
    font-style: normal;
    font-weight: lighter;
}

  
  form {
    width: 500px;
    margin: 0 auto;
    background: white;
    border-radius: 10px;
    margin-bottom: 20px;
    margin-top: 80px;
    box-shadow: 5px 5px 21px 0px rgba(0, 0, 0, 0.75);
  }
  
  input, .floating-placeholder {
    font-family: Helvetica, Arial, sans-serif;
    font-size: 20pt;
    line-height: 2.6em;
    height: 2.6em;
    margin: 0;
    padding: 0;
    width: 100%;
  }
  
  .floating-placeholder input:focus + label {
    color: #16abf0;
  }
  
  .floating-placeholder input[value] + label {
    color: red;
  }
  
  .floating-placeholder {
    position: relative;
  }
  
  .floating-placeholder input {
    font-size: 20pt;
    border: none;
    outline: none;
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    background: transparent;
    z-index: 2;
    border-bottom: 1px solid #ccc;
    text-indent: 20px;
  }
  
  .floating-placeholder:last-child input {
    border-bottom: none;
  }
  
  .floating-placeholder label {
    display: block;
    position: absolute;
    top: 0;
    left: 20px;
    font-size: 20pt;
    z-index: 1;
    -webkit-transform-origin: 0 0em;
    transform-origin: 0 0em;
    transition: color 200ms, -webkit-transform 160ms;
    transition: transform 160ms, color 200ms;
    transition: transform 160ms, color 200ms, -webkit-transform 160ms;
    -webkit-transform: scale(1, 1) rotateY(0);
    transform: scale(1, 1) rotateY(0);
    color: #999;
  }
  
  .floating-placeholder-float label {
    -webkit-transform: scale(0.55, 0.55) rotateY(0);
    transform: scale(0.55, 0.55) rotateY(0);
  }
  
  .floating-placeholder-float input {
    line-height: 3.4em;
  }


.area{
    background: #002D62;  
    background: -webkit-linear-gradient(to left, #FDBB30, #BEC0C2);  
    width: 100%;
    height:100vh;
    
   
}

.circles{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.circles li{
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.2);
    -webkit-animation: animate 25s linear infinite;
            animation: animate 25s linear infinite;
    bottom: -150px;
    
}

.circles li:nth-child(1){
    left: 25%;
    width: 80px;
    height: 80px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}


.circles li:nth-child(2){
    left: 10%;
    width: 20px;
    height: 20px;
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
    -webkit-animation-duration: 12s;
            animation-duration: 12s;
}

.circles li:nth-child(3){
    left: 70%;
    width: 20px;
    height: 20px;
    -webkit-animation-delay: 4s;
            animation-delay: 4s;
}

.circles li:nth-child(4){
    left: 40%;
    width: 60px;
    height: 60px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    -webkit-animation-duration: 18s;
            animation-duration: 18s;
}

.circles li:nth-child(5){
    left: 65%;
    width: 20px;
    height: 20px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}

.circles li:nth-child(6){
    left: 75%;
    width: 110px;
    height: 110px;
    -webkit-animation-delay: 3s;
            animation-delay: 3s;
}

.circles li:nth-child(7){
    left: 35%;
    width: 150px;
    height: 150px;
    -webkit-animation-delay: 7s;
            animation-delay: 7s;
}

.circles li:nth-child(8){
    left: 50%;
    width: 25px;
    height: 25px;
    -webkit-animation-delay: 15s;
            animation-delay: 15s;
    -webkit-animation-duration: 45s;
            animation-duration: 45s;
}

.circles li:nth-child(9){
    left: 20%;
    width: 15px;
    height: 15px;
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
    -webkit-animation-duration: 35s;
            animation-duration: 35s;
}

.circles li:nth-child(10){
    left: 85%;
    width: 150px;
    height: 150px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    -webkit-animation-duration: 11s;
            animation-duration: 11s;
}



@-webkit-keyframes animate {

    0%{
        -webkit-transform: translateY(0) rotate(0deg);
                transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100%{
        -webkit-transform: translateY(-1000px) rotate(720deg);
                transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }

}



@keyframes animate {

    0%{
        -webkit-transform: translateY(0) rotate(0deg);
                transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100%{
        -webkit-transform: translateY(-1000px) rotate(720deg);
                transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }

}
